<template>
    <div class="about-page">
      <AboutSection
        v-if="aboutData.aboutSection"
        :title="aboutData.aboutSection.title"
        :description="aboutData.aboutSection.description"
        :image="aboutData.aboutSection.image"
      />
    </div>
  </template>
  
  <script>
  import AboutSection from '@/components/AboutSection.vue'
  
  export default {
    components: {
      AboutSection
    },
    data() {
      return {
        aboutData: {}
      }
    },
    async created() {
      try {
        const response = await fetch('http://localhost:3000/aboutSection')
        if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`)
        this.aboutData = await response.json()
        console.log('About data loaded:', this.aboutData)
      } catch (error) {
        console.error("Error loading about data:", error)
      }
    }
  }
  </script>
  